.top-text {
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: rgba(157, 164, 189, 0.6);
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  &.about {
    color: #303030;
  }

  &.price {
    font-size: 9px;
    color: rgba(157, 164, 189, 0.6);
  }

  &.school {
    color: var( --bottom-text-color);
  }

  &::before {
    content: "";
    width: 60px;
    height: 1px;
    background-color: currentColor;
    margin-right: 21px;
  }
}
