.masters .conteiner {
    @include tablet {
      padding: 0 160px 0 160px;
    }
  
    @include desktop {
      padding: 0 15px 0 15px;
    }
  }
  
  .list_masters {
    @include desktop  {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
    }
   
  }
  
  .item_masters {
    background-color: var(--text-bottons-color);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
    height: 100%;
  
    @include desktop  {
      flex-basis: calc((100% - 6 * 15px) / 3);
      margin: 15px;
    }
  }
  
  .item_masters:not(:last-child) {
    margin-bottom: 30px;
  
    @include desktop  {
      margin-bottom: 0px;
    }
  }
  
  .magazine_conteiner {
    text-align: center;
    display: block;
  }
  
  .masters_title {
    margin-top: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    letter-spacing: 0.02em;
    color: #303030;
  }
  
  .masters_skils {
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.38;
    letter-spacing: 0.02em;
    color: #303030;
  }